                                    <v-radio label="Option 1" value="radio-1"></v-radio>
                                    <v-radio label="Option 2" value="radio-2"></v-radio>
                                </v-radio-group>
                            </div>
                        </div>
                        <Bar :chart-options="chartOptions" :chart-data="chartData" :chart-id="chartId"
                         
